<template>
  <q-dialog v-model="showDialog">
      <q-card>
        <q-toolbar>
          <q-toolbar-title>Preset-Vorauswahl</q-toolbar-title>

          <q-btn flat round dense icon="close" v-close-popup />
        </q-toolbar>

        <q-card-section>
          <div class="q-mb-md">
            Wir haben für dich je nach Geschmack und Genre verschieden Preset-Sammlungen zusammengestellt. Wähle einfach eine Sammlung aus und starte mit dem Proben. Viel Spaß!
          </div>
        </q-card-section>

        <q-card-section class="flex flex-center">
          <div class="column q-pa-md full-width">

              <q-btn no-caps v-for="preset in roomPresets" :key="preset.id"  
                  color='primary' class="custom-btn full-width q-mb-md"
                  @click="selectRoomPresetCollection(preset.id)"
                  unelevated>
                <template v-slot:default>
                  <div class="btn-content">
                    <div class="btn-heading">{{ preset.name }}</div>
                    <div class="btn-description">{{ preset.description }}</div>
                  </div>
                </template>
              </q-btn>
          </div>
        </q-card-section>

      </q-card>
    </q-dialog>

</template>

<script>
import { computed } from 'vue'
import { useInstrumentStore } from 'stores/instruments-store';
import { getRoomPresetCollections, selectCurrentRoomPresetCollection } from 'src/services/apiServices';

export default {
  data() {
    return {
      showDialog: false,
      roomPresets: null
    };
  },
  methods: {
    fetchData() {
      getRoomPresetCollections()
        .then(response => {
          this.roomPresets = response.data;
        })
        .catch(error => {
          console.error('Error fetching data from room backend:', error);
        });
    },
    show() {
      this.fetchData();
      this.showDialog = true;
    },
    close() {
      this.showDialog = false
    },
    selectRoomPresetCollection(id) {
      const store = useInstrumentStore();

      selectCurrentRoomPresetCollection(id)
        .then(response => {
          const presetCollectionName = response.data.name;
          store.setPresetCollection(presetCollectionName)
        })
        .catch(error => {
          console.error('Error selecting room preset collection:', error);
        });
      this.close();
    }
  }
}
</script>

